<script>
import { ref, onMounted } from 'vue'
import { useRouter } from "vue-router"
import { Toast, ToastConfirm } from '../../components/global/swal'
import { errorHandle } from '../../utils/handle'
import Header from "../../components/global/header/HeaderArea.vue"
import { apiGetResumeInfo, apiDeleteResumeInfo } from "@/api/api"
import { getUrlId } from "@/utils/handle"


export default {
    setup() {
        const urlId = getUrlId().urlId
        const router = useRouter()
        const resumeInfoData = ref()


        onMounted(async () => {
            try {
                resumeInfoData.value = await apiGetResumeInfo({ id: urlId })
                if (resumeInfoData.value.status == false) {
                    let errMessage = errorHandle(resumeInfoData.value)
                    Toast(errMessage)
                }
                resumeInfoData.value = resumeInfoData.value.data
                //資料處理
                // languageInfo
                resumeInfoData.value.languageInfo = []
                resumeInfoData.value.language.forEach(function (item, index) {
                    resumeInfoData.value.languageInfo.push(item.name);
                })
                //licenseInfo證照
                resumeInfoData.value.licenseInfo = []
                if (resumeInfoData.value.license?.mainLicensePhotoName?.length > 0 || resumeInfoData.value.license?.mainLicensePhotoName != null) {
                    resumeInfoData.value.licenseInfo.push(resumeInfoData.value.license.mainLicensePhotoName)
                }
                if (resumeInfoData.value.license?.healthFormPhotoName?.length > 0 || resumeInfoData.value.license?.healthFormPhotoName != null) {
                    resumeInfoData.value.licenseInfo.push(resumeInfoData.value.license.healthFormPhotoName)
                }
                if (resumeInfoData.value.license?.policeCriminalRecordName?.length > 0 || resumeInfoData.value.license?.policeCriminalRecordName != null) {
                    resumeInfoData.value.licenseInfo.push(resumeInfoData.value.license.policeCriminalRecordName)
                }
                if (resumeInfoData.value.license?.otherLicensePhotoName?.length > 0 || resumeInfoData.value.license?.otherLicensePhotoName != null) {
                    resumeInfoData.value.licenseInfo.push(resumeInfoData.value.license.otherLicensePhotoName)
                }
                //experienceInfo照顧經驗
                resumeInfoData.value.experienceInfo = []
                resumeInfoData.value.experience.forEach(item => {
                    resumeInfoData.value.experienceInfo.push(item.careExperience)
                })
                if (resumeInfoData.value.experienceInfo.length > 3) {
                    resumeInfoData.value.experienceInfo = resumeInfoData.value.experienceInfo.slice(0, 3)
                }
                //servicePlaceInfo服務地區
                resumeInfoData.value.workPlaceInfo = []
                resumeInfoData.value.workPlace.forEach(item => {
                    resumeInfoData.value.workPlaceInfo.push(item.city + item.area);
                });
                //timePeriod工作時段
                resumeInfoData.value.timePeriodInfo = []
                resumeInfoData.value.timePeriod.forEach(item => {
                    resumeInfoData.value.timePeriodInfo.push(item.name);
                });
                resumeInfoData.value.timePeriodInfo.reverse()
                if (resumeInfoData.value.otherJobTime != 0) {
                    resumeInfoData.value.timePeriodInfo.push(`${resumeInfoData.value.otherJobTime}小時`);
                }

                // console.log(resumeInfoData.value)
            } catch (error) {
                console.log(error)
            }

        })

        const editData = () => {
            router.push({ path: `/resume/edit/${resumeInfoData.value.id}` })
        }
        const deleteData = async () => {
            const result = await ToastConfirm("確定刪除此筆資料")
            if (result == true) {
                try {
                    let msg = await apiDeleteResumeInfo({ id: urlId })
                    if (msg.status == true) {
                        Toast("刪除成功")
                        router.push({ path: '/resume/list' })
                    } else {
                        Toast(msg.message)
                    }
                } catch (error) {
                    console.log(error)
                }
            }
        }

        return { resumeInfoData, editData, deleteData }
    },
    components: {
        Header
    },
};
</script>

<template>
    <Header title="履歷管理" backToPath="/resume/list" />
    <div class="resumeInfo" v-if="resumeInfoData?.name">
        <div class="imgArea">
            <img :src="resumeInfoData.avatarUrl" alt="avatarUrl" class="card-img" v-if="resumeInfoData.avatarUrl" onerror="this.onerror=null; this.src='https://fakeimg.pl/150x200/'">
            <img src="@/assets/image/noavatar.jpg" class="card-img" v-if="!resumeInfoData.avatarUrl">
        </div>
        <div class="title">{{ resumeInfoData.name }}</div>
        <div class="infoText">
            <div class="main1">
                <div class="title1">性別</div>
                <div class="content1">{{ resumeInfoData.gender == 'female' || resumeInfoData.gender == 'Female' ? '女性' : '男性' }}</div>
            </div>
            <div class="main1">
                <div class="title1">年齡</div>
                <div class="content1">{{ resumeInfoData.age }}歲</div>
            </div>
            <div class="main1">
                <div class="title1">身高</div>
                <div class="content1">{{ resumeInfoData.height }}公分</div>
            </div>
            <div class="main1">
                <div class="title1">體重</div>
                <div class="content1">{{ resumeInfoData.weight }}公斤</div>
            </div>
            <div class="main1">
                <div class="title1">年資</div>
                <div class="content1">{{ resumeInfoData.seniority.name }}</div>
            </div>
            <div class="main1">
                <div class="title1">國籍</div>
                <div class="content1">{{ resumeInfoData.countryName }}</div>
            </div>
            <div class="main1">
                <div class="title1">語言</div>
                <div class="content1">{{ resumeInfoData.languageInfo.join('、') }}</div>
            </div>
        </div>
        <div class="infoText2">
            <div class="main2">
                <div class="title2">履歷身分</div>
                <div class="content2">{{ resumeInfoData.resumeTitle }}</div>
            </div>
            <div class="main2">
                <div class="title2">收費標準</div>
                <div class="content2" v-if="resumeInfoData.wage.home">居家：NT${{ resumeInfoData.wage.home }}/小時、NT${{
                        resumeInfoData.wage.fullDay.home
                }}/日
                </div>
                <div class="content2" v-if="resumeInfoData.wage.home">醫院：NT${{ resumeInfoData.wage.hospital }}/小時、NT${{
                        resumeInfoData.wage.fullDay.hospital
                }}/日
                </div>
            </div>
            <div class="main2">
                <div class="title2">相關資格文件</div>
                <div class="content2" v-if="resumeInfoData.licenseInfo.length > 0">{{
                        resumeInfoData.licenseInfo.join('、')
                }}</div>
                <div class="content2" v-if="resumeInfoData.licenseInfo.length == 0">目前未提供相關資格文件</div>
            </div>

            <div class="main2">
                <div class="title2">照顧經驗及時數</div>
                <div class="content2" v-if="resumeInfoData.experienceInfo.length > 0">{{
                        resumeInfoData.experienceInfo.join('、')
                }}</div>
                <div class="content2" v-if="resumeInfoData.experienceInfo.length == 0">目前尚無照顧經驗及時數</div>
            </div>
            <div class="main2">
                <div class="title2">服務地區</div>
                <div class="content2">{{ resumeInfoData.workPlaceInfo.join('、') }}</div>
            </div>
            <div class="main2">
                <div class="title2">工作時段</div>
                <div class="content2">{{ resumeInfoData.timePeriodInfo.join('、') }}</div>
            </div>
            <div class="main2">
                <div class="title2">自我介紹</div>
                <div class="content2">{{ resumeInfoData.selfIntroduction }}</div>
            </div>
        </div>
        <hr class="divider">
        <div class="mt-4b btn-container">
            <div class="row">
                <div class="col">
                    <button class="btn btn-outline-primary" type="button" @click="editData">修改資料</button>
                </div>
                <div class="col">
                    <button class="btn btn-outline-primary" type="button" @click="deleteData">刪除資料</button>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.resumeInfo {
    margin-top: $header-height;
    margin-bottom: calc($footer-height + 15px);
    min-height: calc(100vh - $footer-height - 15px - $header-height);
    padding-left: 22px;
    padding-right: 22px;
    padding-bottom: 22px;
}

.imgArea {
    display: flex;
    justify-content: center;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;

    img {
        height: 12.5rem;
        width: 9.375rem;
        border-radius: 10px;
    }
}

.title {
    font-size: 1.125rem;
    font-weight: 700;
    color: #000000;
    margin-bottom: 16px;
}

.main1 {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 5px;
    margin-bottom: 5px;

    .title1 {
        font-size: 14px;
        font-weight: 400;
        color: #909090;
        margin-right: 14px;

    }

    .content1 {
        font-size: 16px;
        font-weight: 400;
        color: #333333;
    }
}

.main2 {
    margin-top: 10px;
    margin-bottom: 10px;

    .title2 {
        font-size: 14px;
        font-weight: 400;
        color: nth($secondary-color, 1);
    }

    .content2 {
        font-size: 16px;
        font-weight: 400;
        color: #333333;
    }

}
</style>